@mixin size_element($h: 100%, $w: 100%) {
   height: $h;
   width: $w;
}

@mixin border($thick: 1px, $color: #000, $border_style: solid) {
   border-width: $thick;
   border-color: $color;
   border-style: $border_style;
}

@mixin title_style() {
   color: $clr-white;
   font-style: italic;
   font-family: $f_Popins;
   letter-spacing: 0.3rem;
   transform: scale(1, 1.2);
   // transition: 1s;
   text-shadow:
      2px 2px 5px rgba(255, 255, 255, 0.3),
      3px 3px 10px rgba(255, 255, 255, 0.3),
      5px 5px 30px rgba(255, 255, 255, 0.3);
}

@mixin absolute-center() {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
}

@mixin flex-column {
   display: flex;
   flex-direction: column;
}

@mixin flex-center {
   display: flex;
   align-items: center;
   justify-content: center;
}

@mixin flex-center-column {
   @include flex-center;
   flex-direction: column;
}

@mixin flex-center-vert {
   display: flex;
   align-items: center;
}

@mixin flex-center-horiz {
   display: flex;
   justify-content: center;
}

